import MemoAbstract from "jx/core/memos/MemoAbstract";
export default class DataObject extends MemoAbstract {
    constructor(dataObject) {
        super(Object.assign({ id: "", autosave: false, data: {} }, dataObject));
        this.onchange = new signals.Signal();
        this.data = this.dataObject.data;
    }
    setVar(name, value) {
        super.setVar(name, value);
        this.onchange.dispatch();
    }
    dispatch() {
        this.onchange.dispatch();
    }
}
